import React from 'react';
import s from './Ceramics.module.css';
import { NavLink } from 'react-router-dom';
import Filter from './Filter/Filter';
import Pagination from './Pagination/Pagination';




const Ceramics = ({ ceramics, addProperty, filterArr, setFilterProperty, porertys, setCurrentPage, currentPage, portionProduct }) => {
    let portion = [];
    let minPage = currentPage * portionProduct - portionProduct;
    let maxPage = portionProduct * currentPage;
    if (maxPage > filterArr.length) {
        maxPage = filterArr.length
    }
    for (let i = minPage; i < maxPage; i++) {
        portion.push(filterArr[i])
    }

    let collection = portion.map(c => {
        console.log(filterArr);
        return <NavLink key={c.id} to="ceramics" className={s.link}>
            <div className={s.itemBox}>
                <div className={s.imgBox}>
                    {/* <img src={process.env.PUBLIC_URL + '/images/Ceramics/' + c.firm + '/' + c.collection + '.jpg'} alt="" /> */}
                    <img src={c.link} alt="" />
                </div>
                <div className={s.descriptBox}>
                    <div className={s.descrBigBox}>
                        <div className={s.descrItem}><p>Фирма: </p><span className={s.values}>{c.firm}</span></div>
                        <div className={s.descrItem}><p>Коллекция: </p><span className={s.values}>{c.collection}</span></div>
                        <div className={s.descrItem}><p>Страна: </p><span className={s.values}>{c.country}</span></div>
                        <div className={s.buttonAndPriceBox}>
                            <div className={s.descrItem}><p>Цена: </p><span className={s.values}>{c.price.num}/{c.price.uom}</span></div>
                            <button className={s.button}>show</button>
                        </div>
                    </div>
                </div>
            </div>
        </NavLink>
    })

    return <div><div className={s.ceramicsBox}>
        
        <Filter ceramics={ceramics} addProperty={addProperty} setFilterProperty={setFilterProperty} porertys={porertys} />
        <div className={s.itemsBox}>{collection}
          {filterArr.length ? <div className={s.pogBoxW100}>
                <Pagination portionProduct={portionProduct} setCurrentPage={setCurrentPage} filterArr={filterArr} currentPage={currentPage} />
            </div> : <div className={s.noProductText}>Ничего не найдено</div>
          
        }  
        </div>
    </div>

    </div>
}



export default Ceramics;