import React from 'react';
import s from '../Ceramics.module.css';


const Pagination = ({filterArr, setCurrentPage, portionProduct, currentPage}) => {
    let totalCount = Math.ceil(filterArr.length / portionProduct);
    let arrNumber =[];
    for ( let i = 1; i < Math.ceil(totalCount)+1; i++) {
        arrNumber.push(i);
    }
    let pagination = arrNumber.map(p => {
        return <button className={p === currentPage ? s.butPag + ' ' + s.pagActionButt : s.butPag} onClick={() => setCurrentPage(p)} key={p}>{p}</button>
    })
    return <div className={s.pagBox}>
        <button className={s.pagPrNx} disabled={currentPage === 1} onClick={() => setCurrentPage(currentPage-1)}>Prev</button>
        <div className={s.pagBoxNum}>
            {pagination}
        </div>
        <button className={s.pagPrNx} disabled={currentPage === totalCount} onClick={() => setCurrentPage(currentPage+1)}>Next</button>
    </div>
}

export default Pagination;