import { connect } from "react-redux";
import { addProperty, setCurrentPage, setFilterProperty } from "../../Redux/ceramics-reducer";
import Ceramics from "./Ceramics";

let mapStateToProps = (state) => ({
    ceramics: state.ceramicsReducer.ceramics,
    filterArr: state.ceramicsReducer.filterArr,
    porertys: state.ceramicsReducer.porertys,
    currentPage: state.ceramicsReducer.currentPage,
    portionProduct: state.ceramicsReducer.portionProduct,
})



export default connect(mapStateToProps, {addProperty, setFilterProperty, setCurrentPage})(Ceramics);
